.payment {
  padding-bottom: 75px;
  &-top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 80px 180px;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    margin-top: 10px;
  }
  &__group2 {
    position: relative;
  }
  ul {
    max-width: 320px;
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      display: flex;
      gap: 10px;
      font-size: 16px;
      line-height: 120%;
      i {
        width: 19px;
        flex-shrink: 0;
      }
    }
  }
  &__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 515px;
    pointer-events: none;
    img {
      width: 100%;
      max-width: unset;
      max-height: unset;
    }
  }
  .directions__cards {
    max-width: 100%;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 100px !important;
  }
  .direction-card {
    &__desc {
      display: inline-flex;
      text-align: center;
    }
    &__title {
      margin-bottom: auto;
    }
  }
}
.directions3 {
  padding: 75px 0 0 0;
  .directions__cards {
    margin-top: 50px;
  }
}


@media screen and (max-width: 1450px) {
  .payment {
    padding-bottom: 50px;
  }
  .directions3 {
    padding-top: 50px;
  }
}

@media screen and (max-width: 992px) {
  .payment {
    padding-bottom: 25px;
    .directions__cards {
      grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    }
  }
  .directions3 {
    padding-top: 25px;
  }
}

@media screen and (max-width: 768px) {
  .payment {
    .directions__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 15px;
    }
  }
}

@media screen and (max-width: 576px) {
  .payment {
    &-top {
      gap: 20px;
    }
    ul {
      gap: 10px;
      li {
        font-size: 14px;
      }
    }
    &__img {
      display: none;
    }
    .directions__cards {
      max-width: 350px;
      grid-template-columns: 100%;
      margin-top: 20px !important;
      margin-left: auto;
      margin-right: auto;
    }
  }
  .directions3 {
    .directions__cards {
      max-width: 350px;
      grid-template-columns: 100%;
      margin-top: 20px !important;
      margin-left: auto;
      margin-right: auto;
    }
  }
}