.articles {
  padding-bottom: 150px;
  &-topics {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px 30px;
    margin-top: 30px;
  }
  &__topic {
    position: relative;
    display: flex;
    justify-self: center;
    align-items: center;
    font-size: 18px;
    line-height: 120%;
    font-weight: 500;
    &::after {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: calc(100% - 4px);
      height: calc(100% - 4px);
      background: rgb(233, 239, 244);
      border-radius: 30px;
      pointer-events: none;
      transition: all 0.3s;
    }
    &::before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: calc(100% - 4px);
      height: calc(100% - 4px);
      background: rgb(233, 239, 244);
      border-radius: 30px;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      &::after {
        right: 50%;
        bottom: 50%;
        transform: translate(50%, 50%) rotate(-8deg);
      }
      span {
        color: #EC6118;
      }
    }
    span {
      position: relative;
      z-index: 1;
      width: 100%;
      height: 100%;
      border-radius: 30px;
      padding: 15px 30px;
      &::before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 1px solid rgb(17, 57, 117);
        border-radius: 30px;
        pointer-events: none;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    margin-top: 70px;
  }
}
.article-card {
  position: relative;
  display: flex;
  justify-content: space-between;
  background: rgb(233, 239, 244);
  border-radius: 20px;
  padding: 40px;
  &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% + 8px);
    height: calc(100% + 8px);
    border: 1px solid rgb(17, 57, 117);
    border-radius: 20px;
    pointer-events: none;
  }
  &:hover {
    .article-card__title {
      color: rgba(236, 97, 24, 1);
    }
  }
  &__left {
    position: relative;
    z-index: 1;
    width: 475px;
    margin-right: 60px;
  }
  &__right {
    position: relative;
    z-index: 1;
    width: calc(100% - 535px);
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 20px;
  }
  &__img {
    width: 100%;
    img {
      width: 100%;
    }
  }
  &__desc {
    max-width: 675px;
    width: 100%;
  }
  &__group {
    background: rgb(255, 255, 255);
    border-radius: 68px;
    padding: 15px 30px;
  }
  &__btn {
    flex-shrink: 0;
    span {
      padding-left: 26px;
      padding-right: 26px;
    }
  }
  &__title {
    font-size: 34px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 20px;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    margin-top: 15px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:4;
    -webkit-box-orient: vertical;
  }
  &__line {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.article-card__group {
  display: inline-flex;
  gap: 30px;
}
.article-card__info {
  display: flex;
  align-items: center;
  gap: 7px;
  font-size: 14px;
  line-height: 120%;
  i {
    max-width: 24px;
    flex-shrink: 0;
  }
}
.article {
  padding-bottom: 100px;
  &__group {
    &:not(:first-child) {
      margin-top: 80px;
    }
  }
  &__mob-subgroup {
    display: none;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }
  &__left {
    width: 50%;
    max-width: 795px;
  }
  &__right {
    width: 50%;
    max-width: 655px;
  }
  &__subgroup {
    &:not(:first-child) {
      margin-top: 50px;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 15px;
    }
    ul li {
      display: flex;
      gap: 15px;
      font-size: 16px;
      line-height: 120%;
    }
    ul li span {
      position: relative;
      width: 14px;
      height: 14px;
      flex-shrink: 0;
      background: rgb(84, 151, 202);
      border-radius: 50%;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 17px;
        height: 17px;
        background-image: url("../img/article-icon2.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
    }
  }
  .article-card__group {
    position: relative;
    padding: 0;
    margin-top: 40px;
    margin-left: 40px;
    .line {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 432px;
      pointer-events: none;
      img {
        width: 100%;
      }
    }
  }
  h2 {
    margin-top: 80px;
  }
  .articles-topics {
    margin-bottom: 30px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-size: 16px;
      line-height: 120%;
    }
  }
  &__menu {
    position: relative;
    width: 50%;
    max-width: 655px;
    ul {
      position: relative;
      z-index: 1;
      max-width: 450px;
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      margin-top: 30px;
      li {
        a {
          display: flex;
          align-items: center;
          gap: 15px;
          font-size: 22px;
          line-height: 105%;
          font-weight: 700;
        }
        a:hover {
          color: rgb(236, 97, 24);;
        }
        span {
          position: relative;
          width: 29px;
          height: 29px;
          flex-shrink: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          background: rgb(255, 255, 255);
          border-radius: 50%;
          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 33px;
            height: 33px;
            background-image: url("../img/article-icon.svg");
            background-repeat: no-repeat;
            background-position: center;
            background-size: auto;
          }
        }
      }
    }
  }
  &__menu-wrap {
    position: relative;
    background: rgb(233, 239, 244);
    border-radius: 20px;
    padding: 45px;
  }
  h4 {
    font-family: Inter;
    font-size: 34px;
    line-height: 120%;
    margin-bottom: 15px;
  }
  h5 {
    position: relative;
    z-index: 1;
    font-family: Inter;
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
  }
  &__menu-img {
    position: absolute;
    top: 0;
    right: 0;
    width: 230px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
  &__img {
    position: sticky;
    top: 0;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 0;
      transform: translate(0, -50%) rotate(-1deg);
      width: 100%;
      height: calc(100% + 8px);
      border: 1px solid rgb(17, 57, 117);
      border-radius: 20px;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: rotate(-3deg);
      width: 100%;
      height: 100%;
      background: rgb(233, 239, 244);
      border-radius: 20px;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
  }
  &__info {
    &:not(:first-child) {
      margin-top: 30px;
    }
  }
  &__subtitle {
    position: relative;
    display: inline-block;
    font-family: Soyuz Grotesk;
    font-size: 22px;
    line-height: 120%;
    margin-bottom: 15px;
    i {
      width: 160px;
      img {
        width: 100%;
      }
    }
    span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      pointer-events: none;
    }
  }
}
.estimate {
  position: relative;
  width: 50%;
  max-width: 795px;
  background: rgb(233, 239, 244);
  border-radius: 20px;
  padding: 40px;
  margin-top: 100px;
  .line {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    img {
      height: 100%;
    }
  }
  &__group {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 120%;
    margin-top: 10px;
  }
  &__checkboxes {
    display: inline-flex;
    align-items: stretch;
    background: rgb(255, 255, 255);
    border-radius: 8px;
    margin-top: 30px;
  }
  &__checkbox {
    position: relative;
    &:hover {
      label {
        opacity: 1;
      }
    }
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      opacity: 1;
      &:after {
        opacity: 1;
      }
    }
    label {
      position: relative;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: flex-end;
      gap: 10px;
      padding: 12px 30px;
      font-family: Soyuz Grotesk;
      font-size: 22px;
      line-height: 120%;
      font-weight: 700;
      opacity: 0.5;
      transition: all 0.3s;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: calc(100% - 25px);
        height: calc(100% + 10px);
        transform: translate(-50%, -50%) rotate(-2deg);
        border: 1px solid rgb(17, 57, 117);
        border-radius: 5px;
        pointer-events: none;
        opacity: 0;
        transition: all 0.3s;
      }
      i {
        max-width: 34px;
        flex-shrink: 0;
      }
    }
  }
  &__btn {
    margin-top: 30px;
    span {
      padding-left: 35px;
      padding-right: 35px;
    }
  }
  h4 {
    display: flex;
    align-items: center;
    gap: 8px;
    i {
      max-width: 28px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
    }
  }
  &__group2 {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    min-height: 260px;
    display: none;
    flex-direction: column;
    justify-content: center;
  }
}


@media screen and (max-width: 1450px) {
  .articles {
    padding-bottom: 100px;
  }
  .article-card {
    &__title {
      font-size: 28px;
    }
  }
  .article {
    &__menu {
      ul {
        li {
          a {
            font-size: 18px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .article-card {
    &__left {
      width: 400px;
      margin-right: 30px;
    }
    &__right {
      width: calc(100% - 430px);
      flex-direction: column;
      gap: 15px;
    }
    &__desc {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .articles {
    padding-bottom: 50px;
    &-topics {
      gap: 15px;
    }
    &__topic {
      font-size: 16px;
      span {
        padding: 15px;
      }
    }
    &__cards {
      gap: 30px;
      margin-top: 30px;
    }
  }
  .article-card {
    padding: 30px;
    &__left {
      width: 350px;
      margin-right: 20px;
    }
    &__right {
      width: calc(100% - 370px);
    }
    &__title {
      font-size: 24px;
    }
    &__text {
      font-size: 14px;
    }
  }
  .article {
    padding-bottom: 50px;
    overflow: hidden;
    &__inner {
      margin-top: 30px;
    }
    &__group:not(:first-child) {
      margin-top: 30px;
    }
    &__flex {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      max-width: 100%;
    }
    &__right {
      width: 100%;
      max-width: 100%;
    }
    .order {
      order: -1;
    }
    &__menu {
      order: -1;
      width: 100%;
      max-width: 100%;
    }
    .article-card__group,
    h2,
    .articles-topics {
      display: none;
    }
    &__mob-subgroup {
      display: block;
      margin-bottom: 30px;
      .article-card__group,
      h2,
      .articles-topics {
        display: inline-flex;
      }
    }
    .articles-topics {
      margin-bottom: 0;
    }
    &__subgroup {
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
    .article-card__group {
      margin-top: 0;
      .line {
        width: 360px;
      }
    }
    h2 {
      margin-top: 60px;
    }
    h4 {
      font-size: 30px;
    }
  }
  .estimate {
    max-width: 100%;
    width: 100%;
    text-align: center;
    margin-top: 50px;
    .line {
      display: none;
    }
    h4 {
      justify-content: center;
    }
  }
}

@media screen and (max-width: 768px) {
  .article-card {
    flex-direction: column;
    gap: 0;
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
    }
    &__line {
      display: none;
    }
    &__group {
      width: 100%;
      justify-content: space-between;
      gap: 5px;
      padding: 10px;
      margin-top: -30px;
    }
    &__title {
      font-size: 20px;
      margin-top: 15px;
    }
  }
  .article {
    .article-card__group {
      width: auto;
      gap: 10px;
    }
    &__menu {
      ul {
        li {
          a {
            font-size: 16px;
          }
        }
      }
    }
    &__menu-wrap {
      padding: 30px;
    }
  }
  .estimate {
    padding: 25px 10px;
  }
}

@media screen and (max-width: 576px) {
  .article-card {
    padding: 15px 15px 30px;
    &__btn {
      width: 100%;
    }
  }
  .article {
    &__menu-wrap {
      padding: 15px;
    }
  }
}

@media screen and (max-width: 360px) {
  .article-card {
    &__info {
      font-size: 12px;
    }
  }
}