.certificates {
  &__slider {
    position: relative;
    margin-top: 70px;
  }
  &__swiper {
    overflow: unset;
  }
  .swiper-slide {
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &-active,
    &-active + .swiper-slide,
    &-active + .swiper-slide + .swiper-slide,
    &-active + .swiper-slide + .swiper-slide + .swiper-slide {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__slider-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 18px;
    margin-top: 30px;
  }
  .swiper-button-prev {
    position: relative;
    top: unset;
    left: unset;
    margin-top: 0;
  }
  .swiper-button-next {
    position: relative;
    top: unset;
    right: unset;
    margin-top: 0;
  }
  .swiper-pagination {
    position: static;
    width: auto;
  }
}
.certificate-card {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid rgb(17, 57, 117);
    border-radius: 20px;
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    transform: rotate(3deg);
    width: 100%;
    height: 100%;
    background: rgb(233, 239, 244);
    border-radius: 20px;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    &::before {
      transform: rotate(6deg);
    }
  }
  img {
    position: relative;
    z-index: 1;
    width: 100%;
    border-radius: 20px;
  }
}

@media screen and (max-width: 992px) {
  .certificates {
    &__slider {
      margin-top: 30px;
    }
  }
}