.footer {
  position: relative;
  background: rgba(233, 239, 244, 1);
  border-radius: 50px 50px 0 0;
  padding: 30px 0 45px;
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 600px;
    img {
      height: 100%;
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.footer-top {
  position: relative;
  z-index: 2;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  &__right {
    position: relative;
    display: flex;
    align-items: center;
    gap: 110px;
    &:after {
      content: '';
      position: absolute;
      top: 64%;
      left: 0;
      transform: translate(0, -50%);
      width: 100%;
      height: 0.5px;
      border: 1px solid rgb(177, 195, 222);
    }
  }
  &__logo {
    width: 135px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__group {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 48px;
  }
  &__menu {
    display: flex;
    align-items: center;
    li {
      position: relative;
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      padding: 15px;
      cursor: pointer;
      &:hover {
        color: rgb(236, 97, 24);
      }
      a {
        position: relative;
        z-index: 1;
      }
    }
    .active {
      color: rgb(236, 97, 24);
      margin: 0 15px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 100%;
        height: 100%;
        background: rgb(255, 255, 255);
        border: 1px solid rgb(233, 239, 244);
        border-radius: 50px;
      }
    }
  }
  &__text {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 35px;
    p {
      font-size: 14px;
      line-height: 120%;
      color: rgb(135, 157, 191);
    }
  }
  &__group2 {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 37px;
  }
  &__btn {
    gap: 10px;
    background: rgb(255, 255, 255);
    padding: 0 30px;
    color: rgb(236, 97, 24);
    &:hover {
      color: #041B3F;
    }
    .btn-mob__icon {
      position: relative;
      z-index: 1;
      width: 25px;
      flex-shrink: 0;
    }
    .btn-mob__icon svg {
      path {
        fill: currentColor;
      }
    }
    span {
      color: rgb(236, 97, 24);
    }
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__social {
    max-width: 34px;
    svg {
      max-width: 100%;
      max-height: 100%;
    }
  }
}
.footer-mob {
  position: relative;
  z-index: 1;
  display: none;
}

@media screen and (max-width: 1450px) {
  .footer-top {
    &__right {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .footer-top {
    &__logo {
      width: 95px;
    }
    &__menu {
      li {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .footer-top {
    padding-bottom: 15px;
    &__inner {
      position: relative;
      display: block;
    }
    &__right {
      position: static;
      margin-top: 15px;
      &:after {
        display: none;
      }
    }
    &__group {
      width: 100%;
      align-items: start;
    }
    &__text,
    &__socials {
      display: none;
    }
    &__btn {
      position: absolute;
      top: 0;
      right: 0;
    }
  }
  .footer-mob {
    display: block;
    &__inner {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
      border-top: 1px solid rgb(177, 195, 222);
      padding-top: 30px;
    }
    .footer-top__text {
      display: flex;
    }
    .footer-top__socials {
      display: flex;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__menu {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      gap: 0 20px;
    }
  }
  .footer-mob {
    &__inner {
      align-items: start;
    }
    .footer-top__text {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__img {
      height: auto;
      width: 100%;
      img {
        width: 100%;
      }
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
  }
  .footer-top {
    &__text {
      p {
        font-size: 12px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .footer-top {
    &__logo {
      width: 90px;
    }
    &__btn {
      padding: 0 18px;
    }
  }
}

@media screen and (max-width: 360px) {
  .footer-top {
    &__btn {
      span {
        font-size: 16px;
      }
    }
  }
}