.btn-orange {
  position: relative;
  background: rgb(236, 97, 24);
  border: none;
  border-radius: 60px;
  overflow: hidden;
  &:hover {
    .hover-bg {
      bottom: -4px;
    }
    .hover-bg2 {
      bottom: 0;
    }
    span {
      color: #041B3F;
    }
  }
  span {
    position: relative;
    z-index: 1;
    font-family: Soyuz Grotesk;
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
    padding-top: 15px;
    padding-bottom: 15px;
    transition: all 0.7s;
  }
  .hover-bg {
    position: absolute;
    left: 0;
    bottom: -160px;
    pointer-events: none;
    transition: all 0.8s;
  }
  .hover-bg2 {
    position: absolute;
    right: -10px;
    bottom: -160px;
    pointer-events: none;
    transition: all 0.6s;
  }
  img {
    max-width: unset;
    max-height: unset;
  }
}
