h2 {
  font-size: 60px;
  line-height: 100%;
}
h3 {
  font-size: 46px;
  line-height: 100%;
}
.swiper-button-next,
.swiper-button-prev {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(84, 151, 202);
  border-radius: 50%;
  &:after {
    display: none;
  }
  &:before {
    content: '';
    position: absolute;
    top: 0;
    width: 54px;
    height: 54px;
    border: 1px solid rgb(17, 57, 117);
    border-radius: 50%;
    pointer-events: none;
  }
  &:hover {
    svg path {
      fill: #041B3F;
    }
  }
  svg {
    width: 19px;
    height: auto;
  }
 }
.swiper-button-next {
  &:before {
    right: 0;
  }
  &:hover {
    &:before {
      top: -4px;
      right: -4px;
    }
  }
}
.swiper-button-prev {
  &:before {
    left: 0;
  }
  &:hover {
    &:before {
      top: -4px;
      left: -4px;
    }
  }
}
.swiper-pagination {
  &-bullet {
    width: 15px;
    height: 15px;
    background: rgb(84, 151, 202);
    opacity: 1;
  }
  &-bullet-active {
    width: 48px;
    border-radius: 8px;
  }
}
.form {
  &-head {
    font-family: Soyuz Grotesk;
    font-size: 32px;
    line-height: 120%;
    font-weight: 700;
  }
  &-group {
    border: none;
    padding: 0;
    margin: 0;
    input {
      width: 100%;
      height: 40px;
      background: rgb(255, 255, 255);
      border: 1px solid #879DBF;
      border-radius: 5px;
      font-family: Inter;
      font-size: 16px;
      line-height: 120%;
      color: rgb(17, 57, 117);
      padding: 0 15px;
      &::placeholder {
        color: rgb(135, 157, 191);
      }
      &:focus {
        border-color: rgb(17, 57, 117);
      }
    }
    textarea {
      width: 100%;
      height: 95px;
      background: rgb(255, 255, 255);
      border: 1px solid #879DBF;
      border-radius: 5px;
      font-family: Inter;
      font-size: 16px;
      line-height: 120%;
      color: rgb(17, 57, 117);
      padding: 10px 15px;
      resize: none;
      &::placeholder {
        color: rgb(135, 157, 191);
      }
      &:focus {
        border-color: rgb(17, 57, 117);
      }
    }
  }
  &-check {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      cursor: pointer;
      opacity: 0;
    }
    input:checked ~ label {
      &:after {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      min-height: 28px;
      display: flex;
      align-items: center;
      font-size: 14px;
      line-height: 120%;
      color: rgb(135, 157, 191);
      padding-left: 43px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 28px;
        height: 28px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        background-color: rgb(255, 255, 255);
        border: 1px solid rgb(150, 156, 187);
        border-radius: 3px;
      }
    }
  }
  &-btn {
    width: 100%;
    span {
      padding-left: 18px;
      padding-right: 18px;
    }
  }
  &-info {
    position: relative;
    padding-right: 90px;
  }
  &-text {
    font-size: 14px;
    line-height: 120%;
  }
  &-download {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
    border: 1px solid rgb(236, 97, 24);
    border-radius: 56px;
    padding: 8px 20px;
    i {
      flex-shrink: 0;
      width: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: rgb(236, 97, 24);
    }
  }
  .arrow {
    position: absolute;
    top: 3px;
    right: 0;
  }
}
.navigation {
  padding: 50px 0;
  &__list {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 24px;
    a {
      font-size: 24px;
      line-height: 130%;
      &:hover {
        color: rgb(236, 97, 24);;
      }
    }
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgb(17, 57, 117, 0.8);
  pointer-events: none;
  opacity: 0;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    max-width: 650px;
    width: 100%;
    background: rgb(255, 255, 255);
    border-radius: 20px;
    padding: 60px 120px 45px;
    margin: auto;
    @media screen and (max-width: 992px) {
      padding: 50px;
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    top: 25px;
    right: 25px;
    background: transparent;
    width: 30px;
    padding: 0;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__content {
    position: relative;
    z-index: 1;
  }
  .form_block {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 30px;
  }
  .form-btn {
    margin-top: 20px;
  }
  .line {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    pointer-events: none;
    img {
      height: 100%;
    }
  }
}
.breadcrumbs {
  padding: 50px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 15px;
    font-size: 16px;
    line-height: 120%;
    &:hover {
      font-weight: 600;
    }
  }
  &__item--active {
    font-weight: 600;
  }
}


@media screen and (max-width: 1450px) {
  h2 {
    font-size: 50px;
  }
  h3 {
    font-size: 32px;
  }
}

@media screen and (max-width: 1300px) {
  .btn-orange {
    span {
      font-size: 18px;
    }
  }
  .btn-blue {
    span {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  h2 {
    font-size: 40px;
  }
  .breadcrumbs {
    padding: 30px 0;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 32px;
  }
  h3 {
    font-size: 24px;
  }
  .form {
    &-group {
      input,
      textarea {
        font-size: 14px;
      }
    }
  }
  .modal {
    &__dialog {
      padding: 50px 30px;
    }
  }
}