.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 0 0 30px 30px;
  padding: 10px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 110px;
  }
  &__logo {
    width: 95px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__menu {
    display: flex;
    align-items: center;
    li {
      position: relative;
      font-size: 16px;
      line-height: 120%;
      font-weight: 600;
      padding: 15px;
      cursor: pointer;
      &:hover {
        color: #EC6118;
        a::before {
          width: 100%;
        }
      }
      a {
        position: relative;
        z-index: 1;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          bottom: -8px;
          width: 0;
          height: 10px;
          background-image: url('data:image/svg+xml;charset=UTF-8,%3csvg width=\'100%\' height=\'10px\' viewBox=\'0 0 20 10\' xmlns=\'http://www.w3.org/2000/svg\' fill-rule=\'evenodd\' clip-rule=\'evenodd\' stroke-miterlimit=\'10\'%3e%3cpath fill=\'none\' d=\'M0 0h20v10H0z\'/%3e%3cclipPath id=\'a\'%3e%3cpath d=\'M0 0h20v10H0z\'/%3e%3c/clipPath%3e%3cg clip-path=\'url(%23a)\'%3e%3cpath d=\'M20 7.384c-4.999-.001-5-4.768-9.999-4.768C5 2.616 5 7.384 0 7.384\' fill=\'none\' stroke=\'%23EC6118\'/%3e%3c/g%3e%3c/svg%3e');
          transition: all 0.3s;
        }
      }
    }
    .active {
      color: rgb(236, 97, 24);
      margin: 0 15px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        width: 100%;
        height: 100%;
        background: rgb(255, 255, 255);
        border: 1px solid rgb(233, 239, 244);
        border-radius: 50px;
      }
      a::before {
        display: none;
      }
    }
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 30px;
  }
  &__phone {
    display: flex;
    align-items: center;
    gap: 8px;
    &:hover {
      color: rgba(236, 97, 24, 1);
    }
    i {
      width: 25px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        fill: currentColor;
      }
    }
    span {
      font-size: 16px;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__btn {
    flex-shrink: 0;
    gap: 8px;
    .btn-mob__icon {
      position: relative;
      z-index: 1;
      width: 25px;
      flex-shrink: 0;
      display: none;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.7s;
      }
    }
    span {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
  &__socials {
    display: none;
    gap: 20px;
  }
  &__img {
    display: none;
    pointer-events: none;
  }
  &__mob {
    display: none;
    align-items: center;
    gap: 20px;
  }
}
.header2 {
  position: relative;
  background: rgba(233, 239, 244, 0.85);
  backdrop-filter: blur(15px);
  border-radius: 0 0 20px 20px;
}
.header_fixed {
  position: fixed;
  background: rgba(233, 239, 244, 0.85);
  backdrop-filter: blur(15px);
}
.burger {
  position: relative;
  width: 57px;
  height: 38px;
  cursor: pointer;
  span {
    position: absolute;
    left: 0;
    width: 100%;
    transition: all 0.3s;
  }
  span:nth-child(1) {
    top: 0;
  }
  span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
  }
  span:nth-child(3) {
    bottom: 0;
  }
  &--active {
    span:nth-child(1) {
      top: 10px;
      transform: rotate(-45deg);
    }
    span:nth-child(2) {
      display: none;
    }
    span:nth-child(3) {
      bottom: 12px;
      transform: rotate(45deg);
    }
  }
}

@media screen and (max-width: 1600px) {
  .header {
    &__right {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 1450px) {
  .header {
    &__group {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .header {
    &__left {
      z-index: 11;
    }
    &__right {
      position: fixed;
      z-index: 10;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      flex-direction: column;
      align-items: start;
      background: rgb(233, 239, 244);
      padding: 150px 30px 50px;
      transition: all 0.5s;
      overflow-y: auto;
      &--active {
        left: 0;
      }
      .header__group {
        display: none;
      }
    }
    &__mob {
      position: relative;
      z-index: 11;
      display: flex;
    }
    &__menu {
      flex-direction: column;
      align-items: start;
      .active {
        margin: 0;
      }
    }
    &__socials {
      display: flex;
    }
    &__img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      display: block;
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &__right {
      padding: 150px 15px 50px;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__right {
      gap: 0;
      .header__group {
        width: 100%;
        display: flex;
        border-top: 1px solid rgb(177, 195, 222);
        padding-top: 30px;
      }
    }
    &__mob {
      .header__group {
        display: none;
      }
    }
    &__menu {
      padding-bottom: 15px;
    }
    &__logo {
      width: 75px;
    }
    &__phone {
      display: none;
    }
    .header__btn {
      background: #ffffff;
      padding-left: 20px;
      &:hover {
       .btn-mob__icon svg path {
          fill: rgb(4, 27, 63);
        }
      }
      .btn-mob__icon {
        display: block;
      }
      span {
        color: rgb(236, 97, 24);
        padding-left: 0;
      }
    }
    &__socials {
      margin-top: 30px;
    }
  }
}