.main {
  position: relative;
  padding: 120px 0 70px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 95px);
    background: rgba(233, 239, 244, 1);
    pointer-events: none;
  }
  &__inner {
    position: relative;
    z-index: 2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 50px;
  }
  &__left {
    width: 50%;
  }
  &__right {
    width: 50%;
  }
  &__desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title {
    font-size: 62px;
    line-height: 120%;
    text-transform: uppercase;
    i {
      display: inline-block;
      max-width: 175px;
      img {
        width: 100%;
        margin-bottom: -15px;
      }
    }
    .uppercase {
      font-family: Inter;
      text-transform: uppercase;
    }
    .title-info {
      position: relative;
      display: inline-block;
      font-size: 28px;
      line-height: 120%;
      color: rgb(236, 97, 24);
      margin-left: 10px;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 175px;
        max-height: unset;
      }
    }
  }
  &__text {
    max-width: 655px;
    font-size: 18px;
    line-height: 130%;
    margin-top: 20px;
  }
  &__btn {
    width: 220px;
    height: 220px;
    flex-direction: column;
    gap: 15px;
    border-radius: 50%;
    margin-top: 50px;
    &:hover {
      svg path {
        fill: #041B3F;
      }
    }
    &-icon {
      position: relative;
      z-index: 1;
      width: 90px;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        transition: all 0.7s;
      }
    }
    span {
      padding: 0;
    }
    .hover-bg,
    .hover-bg2 {
      bottom: -115%;
      width: 800px;
      img {
        width: 100%;
      }
    }
    .hover-bg2 {
      width: 1000px;
    }
  }
  &__img {
    position: relative;
    &::after {
      content: '';
      position: absolute;
      top: -6px;
      left: 6px;
      transform: rotate(0.5deg);
      width: 100%;
      height: 100%;
      border: 1px solid #113975;
      border-radius: 40px;
      pointer-events: none;
    }
    img {
      width: 100%;
      border-radius: 40px;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    pointer-events: none;
    max-width: 1085px;
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.info {
  position: relative;
  z-index: 1;
  background: rgba(233, 239, 244, 1);
  margin-bottom: 55px;
  &__cards {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &-card {
    position: relative;
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 18px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: rotate(-1deg) translateX(100%);
      width: 100%;
      height: 100%;
      border: 1px solid rgb(17, 57, 117);
      border-radius: 5px;
      opacity: 0;
      transition: all 0.3s;
    }
    &:hover {
      color: rgb(236, 97, 24);
    }
    &__icon {
      width: 32px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
      svg path {
        fill: currentColor;
      }
    }
    &__text {
      font-size: 18px;
      line-height: 120%;
      font-weight: 500;
    }
  }
  &-card--active {
    &::after {
      transform: rotate(-1deg) translateX(0%);
      opacity: 1;
    }
  }
  &-card2 {
    flex-direction: column;
    align-items: start; 
  }
  &__title {
    font-family: Soyuz Grotesk;
    font-size: 29px;
    line-height: 120%;
    font-weight: 700;
    color: rgb(17, 57, 117);
  }
  &__text {
    font-size: 14px;
    line-height: 120%;
    color: rgb(17, 57, 117);
  }
}
.purposes {
  position: relative;
  padding: 55px 0 150px;
  .container {
    position: relative;
    z-index: 1;
  }
  h3 {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 50px;
  }
  &__img {
    position: relative;
    max-width: 785px;
    transform: rotate(-1deg);
    margin-top: 135px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%) rotate(-1.6deg);
      width: 100%;
      height: calc(100% + 30px);
      background: rgb(233, 239, 244);
      border-radius: 20px;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 0;
      transform: translate(0, -50%) rotate(-1.2deg);
      width: 100%;
      height: 100%;
      border: 1px solid rgb(17, 57, 117);
      border-radius: 20px;
      pointer-events: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
      border-radius: 20px;
    }
  }
  &__img2 {
    position: absolute;
    left: 0;
    bottom: -50px;
    width: 100%;
    min-width: 1920px;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.purpose-card {
  position: relative;
  background: rgba(233, 239, 244, 1);
  border-radius: 40px;
  padding: 30px 20px;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(233, 239, 244, 1);
    border-radius: 40px;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    &:after {
      opacity: 0;
    }
    .purpose-card__show {
      top: -100%;
    }
  }
  &__show {
    position: absolute;
    z-index: 1;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 8px;
    transition: all 0.5s;
  }
  &__icon {
    max-width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__title {
    font-family: Soyuz Grotesk;
    font-size: 24px;
    line-height: 120%;
    font-weight: 700;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
  }
}
.info2 {
  position: relative;
  z-index: 2;
  padding: 150px 0 100px;
  h2 {
    max-width: 795px;
  }
  &__cards {
    display: flex;
    align-items: start;
    gap: 200px;
    margin-top: 50px;
  }
}
.info2-card {
  display: flex;
  align-items: flex-end;
  gap: 40px;
  &:nth-child(even) {
    margin-top: 150px;
  }
  &:hover {
    .info2-card__img {
