        transform: rotate(-8deg) translate(-45%, -50%);
      }
    }
  }
  &__img {
    position: relative;
    max-width: 375px;
    img {
      width: 100%;
      transition: all 0.3s;
    }
    img:nth-child(1) {
      position: relative;
      z-index: 1;
    }
    img:nth-child(2) {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-45%, -50%);
    }
  }
  &__title {
    font-size: 34px;
    line-height: 120%;
    font-weight: 700;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    margin-top: 8px;
  }
  &__btn {
    margin-top: 30px;
    span {
      padding-left: 40px;
      padding-right: 40px;
    }
  }
}
.prices {
  position: relative;
  padding: 100px 0 150px;
  &:after {
    content: '';
    position: absolute;
    top: -300px;
    left: 0;
    width: 100%;
    height: calc(100% + 600px);
    background-image: url("../img/prices-bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    pointer-events: none;
  }
  .container {
    position: relative;
    z-index: 1;
  }
  h2 {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 160px / 3));
    gap: 80px;
    margin-top: 95px;
  }
}
.price-card {
  position: relative;
  &:nth-child(2) {
    &:after {
      background: rgb(17, 57, 117);
    }
    .price-card__price {
      color: rgba(236, 97, 24, 1);
    }
  }
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%) rotate(6deg);
    width: 100%;
    height: calc(100% + 10px);
    border: 1px solid rgb(17, 57, 117);
    border-radius: 20px;
    pointer-events: none;
    transition: all 0.3s;
  }
  &:hover {
    &:after {
      transform: translate(0, -50%) rotate(3deg);
    }
    .line {
      transform: translate(-50%, 0) rotate(3deg);
    }
  }
  &__wrap {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: rgb(255, 255, 255);
    border: 1px solid rgb(17, 57, 117);
    border-radius: 20px;
    padding: 50px 35px;
  }
  &__top {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 15px;
  }
  &__icon {
    max-width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__title {
    font-size: 24px;
    line-height: 120%;
    font-weight: 600;
  }
  .line {
    position: absolute;
    top: -5px;
    left: 50%;
    transform: translate(-50%, 0);
    max-width: 285px;
    width: 100%;
    pointer-events: none;
    transition: all 0.3s;
    img {
      width: 100%;
    }
  }
  &__center {
    margin-top: 60px;
    margin-bottom: auto;
  }
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li {
      display: flex;
      gap: 10px;
      font-size: 16px;
      line-height: 120%;
    }
    li:nth-child(1) {
      p {
        position: relative;
      }
      p:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 2px;
        transform: translate(-50%, 0);
        width: 24px;
        height: 1px;
        background: rgba(236, 97, 24, 1);
      }
    }
    li i {
      width: 20px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-top: 30px;
  }
  &__price {
    font-family: Soyuz Grotesk;
    font-size: 34px;
    line-height: 120%;
    font-weight: 700;
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    color: rgb(116, 130, 152);
  }
  &__btn {
    margin-top: 30px;
    span {
      padding-left: 40px;
      padding-right: 40px;
    }
  }
}
