.questions {
  padding: 220px 0 75px;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 30px;
  }
  &__left {
    max-width: 485px;
    display: flex;
    flex-direction: column;
  }
  &__right {
    max-width: 790px;
    width: 100%;
  }
  &__img {
    max-width: 410px;
    width: 100%;
    margin-top: 5px;
    img {
      width: 100%;
    }
  }
  &__info {
    font-size: 16px;
    line-height: 120%;
    margin-top: 30px;
  }
  &__btn {
    margin-top: 30px;
    span {
      padding-left: 18px;
