      right: 0;
      width: 500px;
      img {
        width: 100%;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__item {
    border: 1px solid rgb(17, 57, 117);
    border-radius: 20px;
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      border-radius: 20px;
      padding: 20px;
      cursor: pointer;
      &:hover {
        background: rgb(233, 239, 244);
        .questions__item-btn {
          background: rgb(84, 151, 202);
          svg path {
            fill: #ffffff;
          }
        }
      }
    }
    &-show--active {
      &:hover {
        background: transparent;
        .questions__item-btn {
          background: transparent;
          svg path {
            fill: rgba(84, 151, 202, 1);
          }
        }
      }
      .questions__item-btn {
        svg:nth-child(1) {
          display: none;
        }
        svg:nth-child(2) {
          display: block;
        }
      }
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 700;
    }
    &-btn {
      width: 40px;
      height: 40px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid rgb(84, 151, 202);
      border-radius: 50%;
      svg {
        width: 28px;
        height: auto;
      }
      svg:nth-child(2) {
        display: none;
      }
    }
    &-hidden {
      position: relative;
      padding: 20px;
      display: none;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 20px;
        width: calc(100% - 80px);
        height: 1px;
        background: rgb(17, 57, 117);
      }
    }
    &-text {
      font-size: 16px;
      line-height: 120%;
    }
  }
}
.school {
