.pools {
  position: relative;
  z-index: 2;
  padding-bottom: 75px;
  .tabs-head {
    display: inline-flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 15px;
    background: rgb(233, 239, 244);
    border-radius: 20px;
    padding: 15px 20px;
    margin-top: 50px;
  }
  .tab {
    border-radius: 15px;
    padding: 15px;
    font-size: 24px;
    line-height: 120%;
    cursor: pointer;
    transition: all 0.3s;
  }
  .tab--active {
    background: rgb(255, 255, 255);
  }
  .tabs-body {
    margin-top: 50px;
  }
  .tabs-content {
    display: none;
    align-items: stretch;
    gap: 30px;
    padding-right: max(30px, calc(100% - 1630px) / 2);
  }
  .tabs-content--active {
    display: flex;
  }
  .map-wrap {
    width: calc(100% - 800px);
  }
  #map,
  #map2 {
    width: 100%;
    height: 100%;
  }
  .contacts__wrap {
    width: 770px;
    max-width: 100% !important;
    border: none;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #ffffff;
      border: 1px solid #113975;
      border-radius: 20px;
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: rotate(3deg);
      width: 100%;
      height: 100%;
      background: rgb(233, 239, 244);
      border-radius: 20px;
      pointer-events: none;
    }
    h2,
    .contacts__group {
      position: relative;
      z-index: 2;
    }
  }
  h2 {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    font-size: 38px;
    i {
      width: 180px;
      img {
        width: 100%;
      }
    }
  }
}
.gallery {
  position: relative;
  padding: 75px 0 150px;
  &:after {
    content: '';
    position: absolute;
    top: -200px;
    left: 0;
    width: 100%;
    height: 1250px;
    background-image: url("../img/gallery-bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .container {
    position: relative;
    z-index: 1;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 60px / 4));
    gap: 20px;
    margin-top: 60px;
  }
  &-card {
    border-radius: 20px;
    &:nth-child(1) {
      grid-column: 1/3;
    }
    &:nth-child(2) {
      grid-column: 3/4;
      grid-row: 1/3;
      padding: 60px 0;
    }
    img {
      width: 100%;
      height: 100%;
      border-radius: 20px;
    }
  }
}


@media screen and (max-width: 1450px) {
  .pools {
    padding-bottom: 50px;
    .map-wrap {
      width: calc(100% - 530px);
    }
    .contacts__wrap {
      width: 500px;
    }
  }
  .gallery {
    padding: 50px 0 100px;
    &:after {
      top: 0;
      height: 1000px;
    }
    &-card {
      &:nth-child(2) {
        padding: 30px 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .pools {
    padding-bottom: 25px;
    .tabs-head {
      margin-top: 30px;
    }
    .tabs-body {
      margin-top: 30px;
    }
    .tabs-content {
      gap: 10px;
      padding: 0;
      margin: 0 15px;
    }
    .map-wrap {
      width: 50%;
    }
    .contacts__wrap {
      width: 50%;
    }
  }
  .gallery {
    padding: 25px 0 50px;
    &:after {
      height: 100%;
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      margin-top: 30px;
    }
    &-card {
      grid-column: unset !important;
      grid-row: unset !important;
      border-radius: 10px;
      padding: 0 !important;
      img {
        border-radius: 10px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .pools {
    .tabs-content {
      flex-direction: column;
    }
    .map-wrap {
      width: 100%;
      height: 50vw;
    }
    .contacts__wrap {
      width: 100%;
    }
  }
  .gallery {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
     gap: 15px;
    }
  }
}

@media screen and (max-width: 576px) {
  .pools {
    .tabs-head {
      display: flex;
      flex-direction: column;
    }
    .map-wrap {
      height: 64vw;
    }
  }
  .gallery {
    &__cards {
      grid-template-columns: 100%;
    }
  }
}